@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --primary-color: hsl(180, 66%, 49%);
  --violet: hsl(257, 27%, 26%);
  --red: hsl(0, 87%, 67%);
  --grey: hsl(0, 0%, 75%);
  --grayish-violet: hsl(257, 7%, 63%);
  --dark-blue: hsl(255, 11%, 22%);
  --footer: hsl(260, 8%, 14%);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}
.wrapper {
  margin: 60px auto;
  width: 80%;
}
.App {
  width: 100%;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 60px auto 30px auto;
  width: 80%;
}
.nav-links,
.login,
.links {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.links {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav-links li,
.login li {
  list-style: none;
}
.logo-header {
  display: flex;
  align-items: center;
}
.responsive-nav {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.nav-links {
  margin-left: 30px;
}
.login li {
  margin-left: 30px;
}
.signup {
  padding: 10px 25px;

  border-radius: 50px;
  background: var(--primary-color);
  cursor: pointer;
}
.signup:hover {
  background: #95e9e9;
}
.nav-links li a,
.login li a {
  font-weight: 700;
  color: var(--grayish-violet);
  padding: 10px 20px;
  cursor: pointer;
}
.nav-links li a:hover,
.login li a:hover {
  color: var(--footer);
}
li a {
  text-decoration: none;
}
.signup a {
  color: white !important;
}
.nav-toggle {
  display: none;
}
@media only screen and (max-width: 880px) {
  .responsive-nav {
    display: none;
  }
  .nav-toggle {
    display: block;
    cursor: pointer;
  }
  .responsive-nav.unfold {
    display: block;
    width: 80%;
    background: var(--violet);
    position: absolute;
    top: 123px;
    border-radius: 10px;
    z-index: 2;
    padding: 30px 0;
  }
  .nav-links,
  .login {
    display: flex;
    flex-direction: column;
    margin: 0;
  }
  .login li {
    margin: 0;
  }
  .nav-links li,
  .login li {
    width: 80%;
    text-align: center;
    padding: 10px 0;
  }
  .login li {
    border-top: 1px solid var(--grey);
  }
  .login li:first-child {
    margin-top: 10px;
    padding: 20px 0;
  }
  .nav-links li a,
  .login li a {
    color: white;
  }
}
@media only screen and (max-width: 980px) {
  .login li a {
    padding: 0;
  }
}

button {
  padding: 10px 25px;
  color: white !important;
  border-radius: 50px;
  background-color: var(--primary-color);
  outline: none;
  border: none;
  cursor: pointer;
}
button:hover {
  background-color: #95e9e9;
}
.hero-text {
  font-weight: 700;
  font-size: 70px;
  line-height: 80px;
  color: var(--dark-blue);
}
.text {
  color: var(--grayish-violet);
  width: 70%;
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 30px;
}
.hero .content {
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.hero {
  height: 500px;
}
.hero-bg {
  overflow: hidden;
}
.text-content {
  margin-left: 10%;
}
@media only screen and (max-width: 500px) {
  .hero {
    height: 100%;
    overflow: hidden;
  }
  .text-content {
    width: 80%;
    text-align: center;
    margin: 0;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .hero-text {
    line-height: 45px;
    font-size: 40px;
  }
  .hero .content {
    display: flex;
    flex-direction: column-reverse;
  }
  .hero-bg img {
    position: relative;
    width: 500px;
    left: 80px;
  }
  .text {
    width: 100%;
    padding-top: 10px;
  }
}
@media only screen and (max-width: 950px) {
  .hero {
    height: 100%;
    overflow: hidden;
  }
  .text-content {
    width: 80%;
    text-align: center;
    margin: 0;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .hero-text {
    line-height: 45px;
    font-size: 40px;
  }
  .hero .content {
    display: flex;
    flex-direction: column-reverse;
  }
  .hero-bg img {
    position: relative;
    width: 100%;
    left: 80px;
  }
  .hero-bg {
    overflow: visible;
    overflow: initial;
  }
}

.urlShortener {
  position: relative;
}
.container {
  background: var(--violet);
  margin: 0 auto;
  width: 80%;
  padding: 50px;

  background-image: url(/link-shortner-react/static/media/bg-shorten-desktop.0d9742f4.svg);
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 10px;
  position: relative;
  top: 77px;
  height: 160px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.form {
  display: grid;
  grid-template-columns: 83% 15%;
  grid-column-gap: 2%;
}
input {
  padding: 15px 25px;
  font-size: 18px;
  border-radius: 10px;
  border: none;
}
input:focus {
  border: 2px solid var(--primary-color);
  outline: none;
}
.shorten-btn {
  padding: 15px 25px;
  color: white !important;
  border-radius: 10px;
  background: var(--primary-color);
  outline: none;
  border: none;
  font-size: 18px;
}
.short-link {
  position: relative;
  top: 100px;
  margin: 0 auto;
  width: 80%;
}
.short-link li {
  list-style: none;
  background: white;
  padding: 15px 25px;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  font-size: 18px;
}
.short {
  color: var(--primary-color);
}
.error {
  color: var(--red);
  padding-top: 10px;
  position: absolute;
  top: 110px;
  display: none;
}
.error-input,
.error-input:focus {
  border: 2px solid #f46262;
}
.error-input::-webkit-input-placeholder {
  color: #f46262;
  opacity: 0.5;
}
.error-input:-ms-input-placeholder {
  color: #f46262;
  opacity: 0.5;
}
.error-input::placeholder {
  color: #f46262;
  opacity: 0.5;
}
.copy-btn {
  padding: 10px 0;

  color: white !important;
  border-radius: 5px;
  background: var(--primary-color);
  outline: none;
  border: none;
  font-size: 16px;
}
.copy-btn:hover,
.shorten-btn:hover {
  background: #95e9e9;
}

.short-copy p {
  padding-right: 30px;
}
@media only screen and (max-width: 768px) {
  .form {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }
  .container {
    padding: 25px;
    height: 200px;
  }
  .error {
    padding: 0;
    top: 85px;
  }
  input,
  .shorten-btn {
    border-radius: 5px;
  }

  .short-link li {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    padding: 0;
  }
  .short-copy {
    display: flex;
    flex-direction: column;
    padding: 20px;
    width: 100%;
  }
  .short-copy p {
    padding-bottom: 15px;
  }
  .copy-btn {
    width: 100%;
  }
  .url {
    width: 100%;
    word-wrap: break-word;
    border-bottom: 1px solid var(--grey);
    padding: 20px;
  }
}
@media only screen and (min-width: 769px) {
  .container {
    padding: 20px;
    height: 160px;
  }
  .form {
    display: grid;
    grid-template-columns: 73% 25%;
    grid-column-gap: 2%;
  }
  .short-link li {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    padding: 0;
  }
  .short-copy {
    display: flex;
    flex-direction: column;
    padding: 20px;
    width: 100%;
  }
  .short-copy p {
    padding-bottom: 15px;
  }
  .copy-btn {
    width: 100%;
  }
  
}
@media only screen and (min-width: 1250px) {
  .form {
    display: grid;
    grid-template-columns: 83% 15%;
    grid-column-gap: 2%;
  }
  .container {
    padding: 25px;
  }
  .short-link li {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .short-copy {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: auto;
  }
  
  .copy-btn {
    width: 120px;
  }
}

.statistics {
  background: #eff1f7;
  height: 920px;
  padding-top: 50px;
}
.statistics .content {
  margin: 60px auto;
  width: 80%;
}
.cards {
  margin-top: 100px;
}
.card {
  background: white;
  padding: 30px;
  border-radius: 5px;
  height: auto;
  width: 360px;
  position: relative;
  z-index: 1;
}
.card:nth-child(2) {
  position: relative;
  top: 50px;
}
.card:nth-child(3) {
  position: relative;
  top: 100px;
}
.circle {
  background: var(--violet);
  padding: 15px;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: -40px;
}
.line {
  background-color: var(--primary-color);
  height: 8px;
  top: -120px;
  position: relative;
  z-index: 0;
}
.card h4 {
  margin-top: 40px;
  font-size: 22px;
  color: var(--dark-blue);
  font-weight: 700;
}
.card p {
  color: var(--grey);
  margin-top: 20px;
}
.statistics-title {
  padding-top: 80px;
  font-size: 40px;
  font-weight: 700;
  text-align: center;
  color: var(--dark-blue);
}
.title-text {
  text-align: center;
  color: var(--grey);
  font-weight: 500;
  width: 40%;
  margin: 0 auto;
}
@media only screen and (max-width: 500px) {
  .cards {
    display: flex;
    flex-direction: column;
    height: 900px;
  }
  .card {
    width: 100%;
  }
  .statistics {
    height: 100%;
    padding-bottom: 100px;
    padding-top: 50px;
  }
  .title-text {
    width: 100%;
  }
  .statistics-title {
    font-size: 28px;
  }
  .circle {
    top: 0;
    left: 50%;
    right: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  .line {
    height: 8px;
    -webkit-transform: translate(-50%, -50%) rotate(90deg);
            transform: translate(-50%, -50%) rotate(90deg);
    width: 700px;
    position: relative;
    left: 50%;
    right: 50%;
    top: -400px;
  }
}
@media only screen and (min-width: 501px) {
  .cards {
    display: flex;
    flex-direction: column;
    /*height: 900px;*/
  }
  /*.card {
    width: 100%;
  }*/

