    width: 100%;
  }
  .statistics-title {
    font-size: 28px;
  }
  .circle {
    top: 0;
    left: 50%;
    right: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  .line {
    height: 8px;
    -webkit-transform: translate(-50%, -50%) rotate(90deg);
