.boost {
  height: 250px;
  background: var(--violet);
  background-image: url(/link-shortner-react/static/media/bg-boost-desktop.772154c9.svg);
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.boost h1 {
  color: white;
  font-weight: 700;
  padding-bottom: 25px;
  font-size: 40px;
}
.footer {
  background: var(--footer);
  height: 350px;
  display: flex;
  align-items: center;
}
.container-footer {
  margin: 0 auto;
  width: 80%;
  flex-direction: row;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.footer-links {
  list-style: none;
}
.footer-links li a {
  color: var(--grey);
  padding-bottom: 10px;
  text-decoration: none;
}
.footer-links li a:hover {
  color: var(--primary-color);
}
.white {
  color: white !important;
  font-weight: 700;
  padding-bottom: 20px !important;
}
.social img {
  padding-left: 25px;
  -webkit-filter: invert(94%) sepia(94%) saturate(23%) hue-rotate(50deg)
    brightness(105%) contrast(107%);
          filter: invert(94%) sepia(94%) saturate(23%) hue-rotate(50deg)
    brightness(105%) contrast(107%);
}
.logo {
  display: flex;
  align-items: center;
  margin-right: 100px;
}
.social img:hover {
  -webkit-filter: invert(100%) sepia(91%) saturate(3582%) hue-rotate(132deg)
    brightness(82%) contrast(97%);
          filter: invert(100%) sepia(91%) saturate(3582%) hue-rotate(132deg)
    brightness(82%) contrast(97%);
}
@media only screen and (max-width: 890px) {
  .container-footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 50px 0;
  }
  .footer {
    height: 100%;
  }
  .logo {
    margin: 0;
  }
  .container-footer div {
    padding-bottom: 30px;
  }
  .footer-links li {
    padding-bottom: 10px;
  }
  .social a img {
    padding: 0 10px;
  }
  .social {
    display: flex;
    justify-content: space-between;
  }
  .boost {
    background-image: url(/link-shortner-react/static/media/bg-boost-mobile.4b04bd89.svg);
  }
  .boost h1 {
    font-size: 28px;
  }
}

